* {
  box-sizing: border-box;
}

#invisible-checkbox {
  opacity: 0;
}

textarea {
  border: 0px solid transparent;
  background-color: #ffffff;
  border-radius: 5px;
  font-size: 1em;
  resize: none;
}

.regular-task-text {
  flex-grow: 1;
  width: 80%;
}

textarea:focus {
  outline: none; /* Removes the default focus outline */
  border: 2px solid rgb(145, 145, 145);
}

ul {
  list-style-type: none;
  padding: 0 0px;
}

.task-list {
  display: flex;
  flex-direction: row;
  background: linear-gradient(rgb(188, 247, 255), rgb(255, 255, 255));
  border-radius: 10px;
  filter: drop-shadow(2px 2px 5px rgba(0, 0, 0, 0.3));
  width: 100%;
  place-items: stretch;
}

.list-container {
  filter: drop-shadow(2px 2px 5px rgba(0, 0, 0, 0.2));
  padding: 2rem;
  border-radius: 10px;
  background-color: #ffffff;
  border: 1px solid rgb(225, 225, 225);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-width: 70%;
}

.list-items-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow-wrap: break-word;
}
.task-container {
  margin: 0.25rem 0;
  display: flex;
  align-items: flex-start;
  overflow-wrap: break-word;
  width: 100%;
}

hr {
  width: 100%;
  border-width: 2px;
}
* {
  color: rgb(0, 0, 0);
  font-family: "Open Sans", serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

.list-title {
  width: 80%;
  font-weight: 600;
  font-size: 1.8rem;
  overflow-wrap: break-word;
  justify-self: flex-start;
  color: rgb(51, 51, 51);
}
.list-header {
  display: flex;
  align-items: start;
  width: 100%;
  justify-content: space-between;
}
.completed-header {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
h3 {
  font-size: 1.5em;
  color: rgb(145, 145, 145);
  font-weight: 600;
}
/* editmode styling */

#edit-mode-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.1);
}
.edit-mode {
  position: absolute;
  justify-content: center;
  filter: drop-shadow(2px 2px 5px rgba(0, 0, 0, 0.2));
  padding: 2rem;
  border-radius: 10px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.new-task {
  background-color: #ffffff;
  border: 0px solid transparent;
}
.title-input {
  font-size: 2em;
  width: 80%;
  border: 0px solid transparent;
}

.close-button {
  background-color: transparent;
  border: none;
  align-self: flex-start;
  margin: 0;
  padding: 0;
}
.flower-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0;
}

#flower-count {
  display: flex;
  align-self: flex-start;
}
#flower-text {
  font-size: 1.4rem;
  font-weight: 600;
  color: rgb(51, 51, 51);
}
